body {
  margin: 0;
  padding: 0;
  font-family: Roboto , sans-serif;
  background-color: white;
  background-image:
    -webkit-gradient(
      linear, 
      left top, right bottom, 
      from(#BDBDBD), to(white)
    );
  background-image:
    -webkit-linear-gradient(
      top left, 
      #BDBDBD, white
    );
  background-image:
    -o-linear-gradient(
      top left, 
      #BDBDBD, white
    );
  background-image:
    linear-gradient(
      to bottom right, 
      #BDBDBD, white
    );
  height:100vh
}
#root {
  height: 100vh
}
/* TODO: break this into flexbox container, veritcal and horizontal alignment*/
.flexbox-container {
	display: -ms-flexbox;
	display: flex;

	-ms-flex-align: center;

	align-items: center;
    
    -ms-flex-pack: center;
    
        justify-content: center;
    
}


